<template>
    <footer class="footer" style="">
        <span class="todo-count"><strong>{{ unDone }}</strong> items left</span>
        <ul class="filters">
            <li><a href="" :class="{selected:statu === 'all'}" @click.prevent="changeStatu('all')">all</a></li>
            <li><a href="" :class="{selected:statu === 'active'}" @click.prevent="changeStatu('active')">active</a></li>
            <li><a href="" :class="{selected:statu === 'completed'}" @click.prevent="changeStatu('completed')">completed</a></li>
        </ul>
        <button class="clear-completed" 
        v-show="isclear"
        @click="removeCompleted">clear completed</button>
    </footer>
</template>

<script>
import {mapGetters,mapState} from 'vuex'
export default {
    name: "Foot",

    data() {
        return {};
    },

    mounted() {
    },
    computed:{
        ...mapGetters(["unDone",'isclear']),
        ...mapState(['statu',])
    },
    methods: {
        changeStatu(statu){
            this.$store.commit('CHANGESTATU',statu)
        },
        removeCompleted(){
            this.$store.commit('REMOVECOMPLETED')
        }
    },
};
</script>

