<template>
    <header class="header">
        <h1>todos</h1>
        <input
            type="text"
            placeholder="what need to be done?"
            class="new-todo"
            @keyup.enter="addTodo($event)"
        />
    </header>
</template>

<script>
export default {
    name: "Header",

    data() {
        return {};
    },

    mounted() {},

    methods: {
        addTodo(e){
            if(!e.target.value) return
            let obj = {
                id: new Date().toISOString(),
                title:e.target.value.trim(),
                done:false
            }
            this.$store.commit('ADDTODO',obj)
            e.target.value = ''
        }
    },
};
</script>

