import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
const state = {
  todos:[
    {id:1, title:'学习vue', done:true,isedit:false},
    {id:2, title:'学习React', done:false,isedit:false},
    {id:3, title:'学习vue-element-admin', done:false,isedit:false},
  ],
  statu:'all'
} 
const mutations = {
  ADDTODO(state,payload){
    state.todos.push(payload)
  },
  CHANGETODODONE(state,payload){
    state.todos.forEach(element => {
      if(element.id === payload.todoId){
        element.done = payload.value
      }
    });
  },
  CLICKALL(state,payload){
    state.todos = state.todos.map(item => {
      item.done = payload.value
      return item
    })
  },
  DELET(state,payload){
    state.todos = state.todos.filter(item => item.id !== payload.value)
  },
  FINISHINP(state,payload){
    state.todos.forEach(item => {
      if(item.id === payload.id){
        item.title = payload.value
      }
    })
    console.log(state.todos);
  },
  DBEDIT(state,payload){
    state.todos.forEach((element,index) => {
      if(element.id === payload.value){
        console.log(element.id);
        let obj = {
          id:element.id,
          title:element.title,
          done:element.done,
          isedit:true
        }
        Vue.set(state.todos,index,obj)
      }
    })
  },
  EDITFALSE(state,payload){
    state.todos.forEach((element,index) => {
      if(element.id === payload.value){
        console.log(element.id);
        let obj = {
          id:element.id,
          title:element.title,
          done:element.done,
          isedit:false
        }
        Vue.set(state.todos,index,obj)
      }
    })
  },
  CHANGESTATU(state,payload){
    state.statu = payload
  },
  REMOVECOMPLETED(state){
    state.todos = state.todos.filter(item => !item.done)
  }
}
const getters = {
  getAllbtn(state){
    return state.todos.every(item => item.done)
  },
  unDone(state){
    return state.todos.filter(item => !item.done).length
  },
  getSatu(state){
    if(state.statu === 'all'){
      return state.todos
    }else if(state.statu === 'active'){
      return state.todos.filter(item => !item.done)
    }else if(state.statu === 'completed'){
      return state.todos.filter(item => item.done)
    }
  },
  isclear(state){
    console.log(state.todos.some(item => item.done));
    return state.todos.some(item => item.done)
  }
}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions: {
  },
  modules: {
  }
})

