<template>
    <section class="main" style="">
        <input
            type="checkbox"
            class="toggle-all"
            :checked="getAllbtn"
            @change="clickAll($event)"
        />
        <ul class="todo-list">
            <li
                class="todo"
                v-for="(todo,index) in getSatu"
                :key="todo.id"
                :class="{ completed: todo.done, editing: todo.isedit }"
               
                @dblclick="Edit(todo.id,todo.title)"
                ref="li"
            >
                <div class="view">
                    <input
                        type="checkbox"
                        class="toggle"
                        :checked="todo.done"
                        @change="todoDone(todo.id, $event)"
                        
                    />
                    <label>{{ todo.title }}</label>
                    <button class="destroy" 
                    @click="delet(todo.id)"
                    ></button>
                </div>
                <input
                    type="text"
                    class="edit"
                    ref="inp"
                    v-show="todo.isedit"
                    @keyup.enter="finishInp(index,todo.id,$event)"
                />
            </li>
            <!-- <li class="todo completed">
                <div class="view">
                    <input type="checkbox" class="toggle" />
                    <label>react</label>
                    <button class="destroy"></button>
                </div>
                <input type="text" class="edit" style="display: none" />
            </li> -->
        </ul>
    </section>
</template>

<script>
import { mapState, mapGetters } from "vuex";
export default {
    name: "Main",

    data() {
        return {
            isEdit: true,
        };
    },
    methods: {
        // 点击todo
        todoDone(todoId, e) {
            this.$store.commit("CHANGETODODONE", {
                todoId,
                value: e.target.checked,
            });
        },
        // 点击选中或取消所有标签
        clickAll(e) {
            this.$store.commit("CLICKALL", { value: e.target.checked });
        },
        delet(todoId,e) {
            this.$store.commit("DELET", { value: todoId });
        },
        // 双击编辑
        Edit(todoId,title){
            this.$store.commit("DBEDIT", { value: todoId }); 
        },
        // 完成编辑
        finishInp(index,id,e){
            if(!e.target.value) return
            console.log(e.target.value);
            this.$store.commit("FINISHINP", {id, value: e.target.value.trim() });
            e.target.value = ''
            this.$store.commit("EDITFALSE", { value: id });
        }
    },
    computed: {
        ...mapState(["todos"]),
        ...mapGetters(["getAllbtn",'getSatu']),
    },
    watch: {},
    mounted() {},
};
</script>

